import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import axios from 'axios';

type quesList = {
  title: string,
  date: string,
  outline: string,
  point: number
}

type evalList = {
  title: string,
  date: string,
  outline: string,
  point: number
}


type pictList = {
  title: string,
  date: string,
  outline: string,
  point: number
}


@Options({
  components: {
    Top_Menu
  },
})

export default class Flower_User_Point_Records extends Vue {
  public store = useStore();
  public ques_List: quesList[] = [];
  public eval_List: evalList[] = [];
  public pict_List: pictList[] = [];

  public mounted() {
    this.quesEvalcount();
  }

  //포인트, 판별, 게시글 조회
  public quesEvalcount() {
    //게시글 작성 유무 조회
    axios.post('http://localhost:3000/flowerQuestionSelect', {id: this.store.state.loginId})
    .then(response => {
      this.ques_List = [];
      for (let i = 0; i < response.data.length; i++) {
        const list: quesList = {title: "", date: "", outline: "", point: 0};
        list.title = response.data[i].Info_title;
        const dateObj = new Date(response.data[i].date);
        list.date = this.formatDate(dateObj);
        list.outline = "게시글 작성";
        list.point = 10;
        this.ques_List.push(list);
      }
    })
    .catch(error => {console.error('에러:', error);});

    //판별한 꽃 조회
    axios.post('http://localhost:3000/flowerEvaluatePointList', {id: this.store.state.loginId})
    .then(response => {
      this.eval_List = [];
      for (let i = 0; i < response.data.length; i++) {
        const list: evalList = {title: "", date: "", outline: "", point: 0};
        list.title = response.data[i].Info_title;
        const dateObj = new Date(response.data[i].date);
        list.date = this.formatDate(dateObj);
        list.outline = "꽃 이름 판별";
        list.point = 5;
        this.eval_List.push(list);
      }
    })
    .catch(error => {console.error('에러:', error);});
    
    //사진 업로드 조회
    axios.post('http://localhost:3000/flowerPicturePointList', {id: this.store.state.loginId})
    .then(response => {
      this.pict_List = [];
      for (let i = 0; i < response.data.length; i++) {
        const list: pictList = {title: "", date: "", outline: "", point: 0};
        list.title = response.data[i].Info_title;
        const dateObj = new Date(response.data[i].date);
        list.date = this.formatDate(dateObj);
        list.outline = "꽃 사진 업로드";
        list.point = 5;
        this.pict_List.push(list);
      }
    })
    .catch(error => {console.error('에러:', error);});
  }

    //날자 문자로 변경
    public formatDate(dateObj: any) {
      const year = dateObj.getFullYear();
      const month = String(dateObj.getMonth() + 1).padStart(2, '0'); // Months are 0-based in JS
      const day = String(dateObj.getDate()).padStart(2, '0');
      return `${year}/${month}/${day}`
    }
  

}
