import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import axios from 'axios';
import { Watch } from 'vue-property-decorator';
import Top_Menu from '../Top_Menu/Top_Menu.vue';

@Options({
    components: {
        Top_Menu
    },
})


export default class Sign_Up extends Vue {
    private store = useStore();
    public terms_service_dialog: boolean = false;
    public privacy_policy_dialog: boolean = false;
    public warning_message: boolean = false;
    public not_sign_up_mail: boolean = false;
    public sign_up_mail_begin: boolean = false;
    public sign_up_mail_available: boolean = false;
    public mail_duplicates: boolean = false;
    public mail_duplicates_check: boolean = false;
    public member_id: string = "";
    public sign_up_id: string = "";
    public sign_up_pass: string = "";
    public sign_up_pass_check: string = "";
    public user_name: string = "";
    public user_phone: string = "";
    public password_find_dialog: boolean = false;
    public password_code_check: boolean = false;
    public password_code_success: boolean = false;
    public password_confirm_check: boolean = false;
    public sign_conditions_check: boolean = false;
    public sign_name_check: boolean = false;
    public sign_phone_check: boolean = false;
    public sign_up_mail_success: boolean = false;
    public sign_up_btn_click: boolean = false;
    public isLoggedIn: boolean = false;

    public mounted() {
    }

    // 이용약관 클릭 이벤트
    public termsServiceDialog() {
        document.body.style.overflow = 'hidden';
        this.terms_service_dialog = true;
    }

    // 다이어 로그 닫을때 
    public termsServiceDialogClose() {
        document.body.style.overflowY = 'auto';
        this.terms_service_dialog = false;
    }

    // 개인정보처리방침 클릭 이벤트
    public privacyPolicyDialog() {
        document.body.style.overflow = 'hidden';
        this.privacy_policy_dialog = true;
    }

    // 다이어 로그 닫을때 
    public privacyPolicyDialogClose() {
        document.body.style.overflowY = 'auto';
        this.privacy_policy_dialog = false;
    }

    //중복확인 버튼 클릭
    public mailDuplication() {
        this.mail_duplicates_check = true;
        axios.post('http://localhost:3000/mailDuplication', { U_id: this.sign_up_id })
            .then(response => {
                const dupli = response.data[0]['count(U_id)'];
                if (dupli === 1) {
                    //중복
                    this.warning_message = true;
                    this.sign_up_mail_begin = true;
                    this.sign_up_mail_success = false;
                    this.sign_up_mail_available = false;
                    this.not_sign_up_mail = false;
                    this.mail_duplicates = false;
                } else {
                    if (this.sign_up_id === "" || !this.isValidEmail(this.sign_up_id) || this.sign_up_id.length > 25) {
                        //빈칸이며 중복확인 버튼을 눌렀을 경우
                        this.not_sign_up_mail = true;
                        this.warning_message = true;
                        this.sign_up_mail_begin = false;
                        this.sign_up_mail_success = false;
                        this.sign_up_mail_available = false;
                        this.mail_duplicates = false;
                    } else {
                        this.not_sign_up_mail = false;
                        this.mail_duplicates = false;
                        this.warning_message = false;
                        this.sign_up_mail_begin = false;
                        this.sign_up_mail_success = true;
                        this.sign_up_mail_available = true;
                    }
                }
            })
            .catch(error => { console.error('에러:', error); });
    }

    //가입하기 버튼 클릭
    public signUpClick() {
        this.sign_up_btn_click = true;
        this.signUpFormblankCheck();

        if (!this.password_code_check && !this.warning_message && this.user_name && this.isValidPhoneNumber(this.user_phone) && !this.password_confirm_check) {
            const userData = {
                U_id: this.sign_up_id,
                U_pass: this.sign_up_pass,
                name: this.user_name,
                phone: this.user_phone
            };
            axios.post('http://localhost:3000/signup', userData)
                .then(response => { console.log(response.data); })
                .catch(error => { console.error('에러:', error); });
            this.store.commit('setLoginStatus', true);
            //this.$router.push({ name: 'Main_Page' });
            this.store.commit('setLoginId', this.sign_up_id);
            this.$router.push({ path: '/Main_Page' });
        } else {
            console.log("불가능")
        }
    }

    //버튼 클릭후 회원가입 빈칸및 사용가능 확인
    public signUpFormblankCheck() {
        //이메일 빈칸 확인 및 제약확인
        if ((this.sign_up_id === "" || !this.isValidEmail(this.sign_up_id)) && this.mail_duplicates_check) {
            this.warning_message = true;
            this.not_sign_up_mail = true;
            this.sign_up_mail_success = false;
            this.sign_up_mail_begin = false;
            this.mail_duplicates = false;
        } else if (!this.mail_duplicates_check) {
            this.mail_duplicates = true;
            this.warning_message = true;
            this.not_sign_up_mail = false;
            this.sign_up_mail_begin = false;
            this.sign_up_mail_success = false;
            this.sign_up_mail_begin = false;
        }

        //이름 빈칸 확인
        if (this.user_name === "") {
            this.sign_name_check = true;
        }
        //전화번호 빈칸 확인 및 제약확인
        if (this.user_phone === "" || !this.isValidPhoneNumber(this.user_phone)) {
            this.sign_phone_check = true;
        }

        if (this.isStrongPassword(this.sign_up_pass) && (this.sign_up_pass.length <= 15 && this.sign_up_pass.length >= 6)) {
            //가능한 암호
            this.password_code_check = false;
        } else {
            this.password_code_check = true;
        }

        //비번 같은지 확인
        if (this.sign_up_pass_check === this.sign_up_pass && this.sign_up_pass_check !== "") {
            this.password_confirm_check = false;
        } else {
            this.password_confirm_check = true;
        }
    }

    // 비번찾기 버튼 클릭 이벤트
    public passwordFindDialog() {
        document.body.style.overflow = 'hidden';
        this.password_find_dialog = true;
    }

    // 다이어 로그 닫을때 
    public passwordFindDialogClose() {
        document.body.style.overflowY = 'auto';
        this.password_find_dialog = false;
    }

    // 비밀번호 제약 확인
    public isStrongPassword(password: string) {
        return password.length >= 6 && password.length <= 15;
    }

    // 이메일 제약 확인
    public isValidEmail(email: string) {
        const emailRegex = /^[A-Za-z0-9._%+-]{5,}@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
        return emailRegex.test(email);
    }

    // 전화번호 제약 확인
    public isValidPhoneNumber(phoneNumber: string) {
        const phoneNumberRegex = /^\d{11}$/;
        return phoneNumberRegex.test(phoneNumber);
    }

    //사용가능 비밀번호 동적확인
    @Watch('sign_up_pass')
    public passWordCheckWatch() {
        if (this.sign_up_btn_click) {
            if (this.isStrongPassword(this.sign_up_pass) && (this.sign_up_pass.length <= 15 && this.sign_up_pass.length >= 6)) {
                //가능한 암호
                this.password_code_check = false;
                this.password_code_success = true;
            } else {
                this.password_code_check = true;
                this.password_code_success = false;
            }
        } else if (this.isStrongPassword(this.sign_up_pass)) {
            this.password_code_success = true;
        } else if (!this.isStrongPassword(this.sign_up_pass)) {
            this.password_code_success = false;
        }

    }

    //비밀번호 같은지 동적확인
    @Watch('sign_up_pass_check')
    public signUpPassWordCheck() {
        if (this.sign_up_btn_click) {
            if (this.sign_up_pass === this.sign_up_pass_check) {
                this.password_confirm_check = false;
            }
        }
    }

    //이름 글자 체크
    @Watch('user_name')
    public signUpNameCheck() {
        if (this.user_name.length <= 20) {
            this.sign_name_check = false;
        } else {
            this.sign_name_check = true;
        }
    }

    //전화번호 글자 체크
    @Watch('user_phone')
    public signUpPhoneCheck() {
        if (this.sign_up_btn_click) {
            if (this.user_phone.length == 11) {
                this.sign_phone_check = false;
            } else {
                this.sign_phone_check = true;
            }
        } else {
            if (this.user_phone.length > 11) {
                this.sign_phone_check = true;
            } else {
                this.sign_phone_check = false;
            }
        }
    }

    public signOut() {
    }
}
