import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import Flower_User_Information_Edit_Dialog from '../Flower_User_Information_Edit_Dialog/Flower_User_Information_Edit_Dialog.vue'
import axios from 'axios';

@Options({
  components: {
    Top_Menu,
    Flower_User_Information_Edit_Dialog
  },
})

export default class Flower_User_Information_Edit extends Vue {
  public store = useStore();
  public user_name: string = "";
  public user_addres: string = "";
  public user_phone: string = "";
  public user_pass: string = "";
  public addres: string = "";
  public use_edit: boolean = false;

  public mounted() {
    this.userInfoGet()
  }

  //유저 정보 가져오기
  public userInfoGet() {
    axios.post('http://localhost:3000/userInfoGet', { id: this.store.state.loginId })
      .then(response => {
        this.user_name = response.data[0].U_name;
        this.user_addres = response.data[0].U_id;
        this.addres = response.data[0].U_id;
        this.user_phone = response.data[0].U_phone;
        this.user_pass = response.data[0].U_pass;
      })
      .catch(error => { console.error('에러:', error); });
    this.$emit('deleteTrue');
  }

  //정보 수정하기
  public editBtnClick() {
    this.use_edit = true;
    const editUser = {
      name: this.user_name,
      addres: this.user_addres,
      phone: this.user_phone,
      pass: this.user_pass,
    }
    axios.post('http://localhost:3000/userInfoUpdate', { editUser: editUser, beforAd: this.addres })
      .then(response => {
        // this.use_edit = true
      })
      .catch(error => { console.log('에러', error); });
  }

  // 다이얼로그 닫고, 비번창 비우기
  public editDialogClose() {
    this.use_edit = false;
    this.user_pass = "";
    this.$router.push({ path: '/Flower_User_Information_Edit' });
  }
}
