import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import axios from 'axios';

@Options({
  components: {

  },
})


export default class Flower_User_Delete_Dialog extends Vue {
  public store = useStore();

  public mounted() {
  }

  // 첫번째 다이얼로그 닫기신호 보내기
  public userCancel() {
    this.$emit('exit');
  }

  //두번째 다이얼로그 열기신호 보내기
  public userClick() {
    axios.post('http://localhost:3000/userDelete', {id: this.store.state.loginId})
    .then()
    .catch(error => {console.error('에러:', error);});
    this.$emit('deleteTrue');

  }
}
