import { createRouter, createWebHistory } from "vue-router";
import store from '../store';
import Flower_Search from '../Flower_Search/Flower_Search.vue';
import Info_Board from '../Info_Board/Info_Board.vue';
import Flower_Question from '../Flower_Question/Flower_Question.vue';
import Flower_Info_Upload from '../Flower_Info_Upload/Flower_Info_Upload.vue';
import Login_Page from '../Login_Page/Login_Page.vue';
import Sign_Up from '../Sign_Up/Sign_Up.vue';
import Flower_User_Information from '../Flower_User_Information/Flower_User_Information.vue'
import Flower_User_Information_Edit from '../Flower_User_Information_Edit/Flower_User_Information_Edit.vue'
import Flower_User_Question_List from '../Flower_User_Question_List/Flower_User_Question_List.vue'
import Flower_User_Evaluate_List from '../Flower_User_Evaluate_List/Flower_User_Evaluate_List.vue'
import Flower_User_Point_Records from '../Flower_User_Point_Records/Flower_User_Point_Records.vue'
import Flower_Point_Trans_Cash from '../Flower_Point_Trans_Cash/Flower_Point_Trans_Cash.vue'
import Flower_User_Account_Update from '../Flower_User_Account_Update/Flower_User_Account_Update.vue'
import Main_Page from '../Main_Page/Main_Page.vue'

const routes = [
  {
    path: '/Main_Page',
    name: "Main_Page",
    component: Main_Page,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_Search',
    name: "Flower_Search",
    component: Flower_Search,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Info_Board',
    name: "Info_Board",
    component: Info_Board,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_Question',
    name: "Flower_Question",
    component: Flower_Question,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_Info_Upload',
    name: "Flower_Info_Upload",
    component: Flower_Info_Upload,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Login_Page',
    name: "Login_Page",
    component: Login_Page,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Sign_Up',
    name: "Sign_Up",
    component: Sign_Up,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Information',
    name: "Flower_User_Information",
    component: Flower_User_Information,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Information_Edit',
    name: "Flower_User_Information_Edit",
    component: Flower_User_Information_Edit,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Question_List',
    name: "Flower_User_Question_List",
    component: Flower_User_Question_List,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Evaluate_List',
    name: "Flower_User_Evaluate_List",
    component: Flower_User_Evaluate_List,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Point_Records',
    name: "Flower_User_Point_Records",
    component: Flower_User_Point_Records,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_Point_Trans_Cash',
    name: "/Flower_Point_Trans_Cash",
    component: Flower_Point_Trans_Cash,
    meta: {
      requiresHideAppElements: true,
    },
  },
  {
    path: '/Flower_User_Account_Update',
    name: "/Flower_User_Account_Update",
    component: Flower_User_Account_Update,
    meta: {
      requiresHideAppElements: true,
    },
  }
];


const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.requiresHideAppElements) {
    store.commit('setShowLinks', false);
  } else {
    store.commit('setShowLinks', true);
  }
  next();
});


export default router; 