import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import axios from 'axios';

type flowerInfo = {
  info_id: number,
  info_title: string,
  u_id: string,
  date: string,
  discrimination: number
  f_picture: string
}

@Options({
  components: {
    Top_Menu
  },
})



export default class Info_Board extends Vue{
  public store = useStore();
  public flower_info: flowerInfo[] = [];
  public flower_picture: string[] = [];
  
  public mounted() {
    //꽃사진 가져오기
    axios.post('http://localhost:3000/flowerPictureGet')
    .then(response => {
      for(let i = 0; i < response.data.length; i++) {
        this.flower_picture[i] = response.data[i].P_url;
      }
      this.flowerListDictionary();
    })
    .catch(error => {console.error('에러:', error);});
      
  }

  //꽃 목록 조회
  public flowerListDictionary() {
    this.flower_info = [];
    axios.post('http://localhost:3000/flowerInfoSelect')
    .then(response => {
      for(let i = 0; i < response.data.length; i++) {
        const flower: flowerInfo = {info_id: 0, info_title: "", u_id: "", date: "", discrimination: 0, f_picture: ""};
        flower.info_id = response.data[i].Info_id;
        flower.info_title = response.data[i].Info_title;
        flower.u_id = response.data[i].U_id;
        flower.date = response.data[i].date;
        flower.discrimination = response.data[i].dis;
        flower.f_picture = this.flower_picture[i];
        this.flower_info.push(flower);
      }
    })
    .catch(error => {console.error('에러:', error);});

  }

  //꽃 판별 유무값 저장
  public flowerDisResult(info_id: number) {
    this.store.commit('setInfoId', info_id);
    for (let i = 0; i < this.flower_info.length; i++) {
      if(info_id === this.flower_info[i].info_id) {
        this.store.commit('setFlowerInfo', this.flower_info[i]);
      }
    }
  }


}