import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import Flower_Question_Dialog from '../Flower_Question_Dialog/Flower_Question_Dialog.vue';
import axios from 'axios';

@Options({
  data() { },
  components: {
    Top_Menu,
    Flower_Question_Dialog
  },
})

export default class Flower_Question extends Vue {
  public store = useStore();
  public startX = 0;
  public startY = 0;
  public offsetX = 0;
  public offsetY = 0;

  public img: string = "";
  public img_select: boolean = false;
  public check_img: boolean = false;
  public flower_question_title: string = "";
  public question_title_blank: boolean = false;
  public info_id: string = "";
  public open_dialog: boolean = false;


  public mounted() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.addEventListener("mousedown", this.onMouseDown);
    }
  }

  //div 박스에서 이미지업로드
  public imageUpload() {
    const image = this.$refs.file as HTMLInputElement;
    if (image) {
      image.click();
    }
  }

  // 이미지 동적으로 불러오가
  public changeFile(event: any) {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target) {
          this.img_select = true;
          this.img = e.target.result as string;
        }
      };
      reader.readAsDataURL(file);
    }
  }


  // 이미지 움직이기
  // 모듈오류생기기 전에 코드 마우스 이벤트 제거
  public beforeDestroy() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.removeEventListener("mousedown", this.onMouseDown);
    }
  }

  // 마우스 클릭 시 초기 좌표 및 이벤트 설정
  public onMouseDown = (e: MouseEvent) => {
    e.preventDefault();

    this.startX = e.clientX;
    this.startY = e.clientY;

    this.offsetX = this.getOffsetLeft() - this.startX;
    this.offsetY = this.getOffsetTop() - this.startY;

    document.addEventListener("mousemove", this.onMouseMove);
    document.addEventListener("mouseup", this.onMouseUp);
  };

  // 마우스가 움직일 때 이미지 위치 업데이트
  public onMouseMove = (e: MouseEvent) => {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.style.left = `${e.clientX + this.offsetX}px`;
      imgObj.style.top = `${e.clientY + this.offsetY}px`;
    }
  };

  // 마우스 놓으면 이벤트 제거
  public onMouseUp = () => {
    document.removeEventListener("mousemove", this.onMouseMove);
    document.removeEventListener("mouseup", this.onMouseUp);
  };

  // 이미지의 왼쪽으로 어느정도 이동했는가를 반환
  public getOffsetLeft() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    return parseInt(imgObj?.style.left || "0", 10);
  }

  // 이미지의 상단으로 어느정도 이동했는가를 반환
  public getOffsetTop() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    return parseInt(imgObj?.style.top || "0", 10);
  }

  // 이미지 초기화하기
  public imgReset() {
    this.img_select = false;
    this.img = "";
  }

  // 다이얼로그 닫기
  public questionCloseDialog() {
    this.open_dialog = false;
    this.imgReset()
  }

  //이미지 따기
  public drawImage() {
    if (this.flower_question_title !== "") {
      this.open_dialog = true;
      this.question_title_blank = false;
      // Canvas 요소 생성
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");

      // Canvas 크기 설정
      canvas.width = 600; // 캔버스 너비
      canvas.height = 600; // 캔버스 높이

      // 이미지를 Canvas에 그림
      const imgObj = document.getElementById("imgObj") as HTMLImageElement; // 이미지 요소 가져오기

      if (imgObj && ctx) {
        ctx.beginPath();
        ctx.arc(canvas.width / 2, canvas.height / 2, (canvas.width / 2) - 50, 0, Math.PI * 2);
        ctx.closePath();
        ctx.clip();
        const imgLeft = this.getOffsetLeft();
        const imgTop = this.getOffsetTop();

        ctx.drawImage(imgObj, imgLeft, imgTop);

        //const dataURL = canvas.toDataURL();

        // const link = document.createElement("a");
        // link.download = "clipped_image.png";
        // link.href = dataURL;
        // link.click();

        //Info테이블 집어 넣기
        axios.post('http://localhost:3000/flowerInfoInput', { title: this.flower_question_title, id: this.store.state.loginId })
          .then(response => {
            console.log(response.data)
            this.info_id = response.data;
            //사진 집어 넣기
            axios.post('http://localhost:3000/flowerPictureInput', { Info_id: this.info_id, id: this.store.state.loginId, url: canvas.toDataURL('image/png') })
              .then(response => {
              })
              .catch(error => { console.error('에러:', error); });
          })
          .catch(error => { console.error('에러:', error); });
        this.pointEarn();
      }
    } else {
      this.question_title_blank = true;
    }
  }

  //포인트 적립하기
  public pointEarn() {
    axios.post('http://localhost:3000/earnPoints', { point: 10, id: this.store.state.loginId })
      .then(response => {
      })
      .catch(error => { console.error('에러:', error); });
  }
}

