import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';

@Options({
  components: {
  },
})



export default class Top_Menu extends Vue {
  public store = useStore();
  public menu_click: boolean = false;
  public mounted() {
  }

  // 탑메뉴 메뉴바 열기 and 닫기, 세줄 이미지 돌리기
  public topMenuDropDown() {
    this.menu_click = !this.menu_click;
    const menu = document.getElementsByClassName('top-responsive-menu-bar');
    const menu_bar = menu[0] as HTMLElement;
    if (this.menu_click) {
      menu_bar.style.transform = 'rotate(90deg)';
      menu_bar.style.transition = 'transform 0.1s';
    } else {
      menu_bar.style.transform = 'rotate(0deg)';
      menu_bar.style.transition = 'transform 0.1s';
    }
  }

  public logOut() {
    this.store.commit('setLoginStatus', false);
    this.$router.push({ path: '/Main_Page' });
  }
}