import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import axios from 'axios';

type discriminationList = {
  info_id: number,
  info_title: string,
  u_id: string,
  date: string,
  discrimination: number
  f_picture: string
}

@Options({
  components: {
    Top_Menu
  },
})

export default class Flower_User_Evaluate_List extends Vue {
  public store = useStore();
  public discrimination_list: discriminationList[] = [];

  public mounted() {
    this.flowerDisSelect();
  }

  //자신이 판별한 게시글 찾기
  public flowerDisSelect() {
    axios.post('http://localhost:3000/flowerDisSelect', {id: this.store.state.loginId})
    .then(response => {
      this.discrimination_list = [];
      for(let i = 0; i < response.data.length; i++) {
        const list: discriminationList = {info_id: 0, info_title: "", u_id: "", date: "", discrimination: 0, f_picture: ""};
        list.info_id = response.data[i].Info_id;
        list.info_title = response.data[i].Info_title;
        list.u_id = response.data[i].U_id;
        list.discrimination = response.data[i].dis;
        const dateObj = new Date(response.data[i].date);
        list.date = this.formatDate(dateObj);
        list.f_picture = response.data[i].P_url;
        this.discrimination_list.push(list);
      }
    })
    .catch(error => {console.error('에러:', error);});
  }

  //날자 문자로 변경
  public formatDate(dateObj: any) {
    const year = dateObj.getFullYear();
    const month = String(dateObj.getMonth() + 1).padStart(2, '0'); // Months are 0-based in JS
    const day = String(dateObj.getDate()).padStart(2, '0');
    return `${year}/${month}/${day}`
  }

    //클릭한 게시글 이동
    public evaluateListClick(info_id: number) {
      this.store.commit('setInfoId', info_id);
      for (let i = 0; i < this.discrimination_list.length; i++) {
        if(info_id === this.discrimination_list[i].info_id) {
          this.store.commit('setFlowerInfo', this.discrimination_list[i]);
        }
      }
      this.$router.push({ path: '/Flower_Info_Upload' });
    }
}
