import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import axios from 'axios';

type questionList = {
  info_id: number,
  info_title: string,
  u_id: string,
  date: string,
  discrimination: number
  f_picture: string
}

@Options({
  components: {
    Top_Menu
  },
})

export default class Flower_User_Question_List extends Vue {
  public store = useStore();
  public question_list: questionList[] = [];

  public mounted() {
    this.flowerQuestionSelect();
    
  }

  //자신이 질문한 게시글 찾기
  public flowerQuestionSelect() {
    axios.post('http://localhost:3000/flowerQuestionSelect', {id: this.store.state.loginId})
    .then(response1 => {
      this.question_list = [];
      for(let i = 0; i < response1.data.length; i++) {
        axios.post('http://localhost:3000/flowerInfoPictureGet', {info_id: response1.data[i].Info_id})
        .then(response2 => {
          const list: questionList = {info_id: 0, info_title: "", u_id: "", date: "", discrimination: 0, f_picture: ""};
          list.info_id = response1.data[i].Info_id;
          list.info_title = response1.data[i].Info_title;
          list.u_id = response1.data[i].U_id;
          list.discrimination = response1.data[i].dis;
          const dateObj = new Date(response1.data[i].date);
          list.date = this.formatDate(dateObj);
          list.f_picture = response2.data[0].P_url;
          this.question_list.push(list);
        })
        .catch(error => {console.error('에러:', error);});
      }
    })
    .catch(error => {console.error('에러:', error);});
  }

  //날자 문자로 변경
  public formatDate(dateObj: any) {
    const year = dateObj.getFullYear();
    const month = String(dateObj.getMonth() + 1).padStart(2, '0'); // Months are 0-based in JS
    const day = String(dateObj.getDate()).padStart(2, '0');
    return `${year}/${month}/${day}`
  }

  //클릭한 게시글 이동
  public questionListClick(info_id: number) {
    this.store.commit('setInfoId', info_id);
    for (let i = 0; i < this.question_list.length; i++) {
      if(info_id === this.question_list[i].info_id) {
        this.store.commit('setFlowerInfo', this.question_list[i]);
      }
    }
    this.$router.push({ path: '/Flower_Info_Upload' });
  }

}
