<template>
    <Top_Menu />
    <div class="info-upload-main" v-if="store.state.flowerInfo.discrimination === 0">
        <div class="info-show-text">
            이 꽃을 찾아줘...
        </div>
        <div class="info-upload-img-box">
            <img class="info-upload-img" :src="flower_picture">
        </div>
        <div v-if="!percentage_results">
            <div class="info-select-box" @click="toggleDropdown" :class="{ 'none': other_flower_input }">
                <p class="select-options-box">{{ selected_Flower ? selected_Flower : '이건 무슨 꽃인가요?' }}</p>
                <div v-if="click_check" class="info-flower-list">
                    <div v-if="click_check">
                        <div v-for="o in flower_name_list" :key="o" class="info_flower-list-items"
                            @click.stop="selectFlower(o)">
                            <a>
                                <span>{{ o }}</span>
                            </a>
                        </div>
                        <div class="info_flower-list-items" @click="otherFlower">
                            <a>
                                <span>+ 기타</span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="other_flower_input" class="info-new-flower-name-box">
                <input type="text" class="info-new-flower-name" placeholder="당신이 생각하는 꽃의 이름을 적어주세요"
                    v-model="selected_Flower">
                <input class="info-new-flower-name-return-btn" title="돌아가기" type="button" @click="infoSelectReset">
            </div>

            <div>
                <input class="info-send-btn" type="button" value="판별 하기" @click="floerNameInput" />
            </div>
        </div>
        <div v-if="percentage_results">
            <div class="info_result_text_box" v-for="(flower, index) in flower_results" :key="index">
                <p>{{ flower.f_name }}: {{ flower.percentage }}%</p>
            </div>

            <div>
                <input class="send-img-button" type="button" value="수정 하기" @click="voteReset" />
            </div>
        </div>
    </div>

    <div class="question-img-main" v-if="store.state.flowerInfo.discrimination === 1">
        <div class="question-main-left">
            <div class="question-img-info">
                <p class="question-img-text">
                    <span class="question-img-text-accent">★이 꽃을 찾아줘..</span>
                    <br>
                    판별이 완료된 꽃 사진입니다.
                    <br>
                    해당 사진을 올려주세요
                </p>
                <img class="question-left-img" :src="flower_picture">
                <p class="question-img-result-text">꽃 : 개나리</p>
            </div>
        </div>
        <div class="qustion-main-right">
            <div class="question-img-upload" @click="imageUpload">
                <p v-if="!img_select">꽃사진 업로드</p>
                <img v-show="img" class="question-img-circle" src="../assets/images/img-circle.png" alt="no">
                <img v-show="img" class="question-img" :src=img id="imgObj" draggable="true">
                <input v-if="!img" ref="file" accept="image/*" type="file" style="display: none" @change="changeFile" />
            </div>
        </div>
        <div class="send-img-button-box">
            <input class="send-img-button" type="button" value="꽃사진 올리기" @click="flowerPictureUpload" />
        </div>
    </div>

    <div class="question-responsive-img-main" v-if="store.state.flowerInfo.discrimination === 1">
        <div class="question-responsive-top">
            <img class="question-responsive-img" :src="flower_picture">
            <div class="question-responsive-text">
                <span class="question-responsive-text-in">★이 꽃을 찾아줘..</span>
                <br>
                판별이 완료된 꽃 사진입니다.
                <br>
                해당 사진을 올려주세요
            </div>
        </div>

        <div class="send-img-responsive-button-box">
            <input class="send-img-button" type="button" value="꽃사진 올리기" @click="flowerPictureUpload" />
        </div>
    </div>
</template>

<script src="./Flower_Info_Upload.ts"></script>
<style src="./Flower_Info_Upload.css"></style>
