import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import Flower_User_Delete_Dialog from '../Flower_User_Delete_Dialog/Flower_User_Delete_Dialog.vue';
import Flower_User_Delete_Complete_Dialog from '../Flower_User_Delete_Complete_Dialog/Flower_User_Delete_Complete_Dialog.vue';
import axios from 'axios';

@Options({
  components: {
    Top_Menu,
    Flower_User_Delete_Dialog,
    Flower_User_Delete_Complete_Dialog
  },
})

export default class Flower_User_Information extends Vue {
  public store = useStore();
  public delete_btn_click: boolean = false;
  public delete_btn_true_click: boolean = false;
  public question_count: number = 0;
  public evaluate_count: number = 0;
  public poiont_num: number = 0;

  public mounted() {
    this.quesEvalcount();

  }

  //포인트, 판별, 게시글 개수 조회
  public quesEvalcount() {
    //게시글개수
    axios.post('http://localhost:3000/flowerQuestionSelect', { id: this.store.state.loginId })
      .then(response => {
        this.question_count = response.data.length;
      })
      .catch(error => { console.error('에러:', error); });

    //판별개수
    axios.post('http://localhost:3000/flowerEvaluateCount', { id: this.store.state.loginId })
      .then(response => {
        this.evaluate_count = response.data.length;
      })
      .catch(error => { console.error('에러:', error); });

    //포인트 조회
    axios.post('http://localhost:3000/userPointSelect', { id: this.store.state.loginId })
      .then(response => {
        this.poiont_num = response.data[0].U_point;
      })
      .catch(error => { console.error('에러:', error); });

  }

  // 첫번째 다이얼로그 열기
  public delDialog() {
    this.delete_btn_click = true;
  }

  // 첫번째 다이얼로그 닫기
  public deleteCloseDialog() {
    this.delete_btn_click = false;
  }

  // 첫번째 다이얼로그 닫기, 두번째 다이얼로그 열기
  public delTrue() {
    this.delete_btn_click = false;
    this.delete_btn_true_click = true;
  }

  // 두번째 다이얼로그 닫고, 메인페이지로 이동하기
  public completeDialogOff() {
    this.delete_btn_true_click = false;
    this.$router.push({ path: '/Main_Page' });
  }
}
