import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import Top_Menu from '../Top_Menu/Top_Menu.vue';
import axios from 'axios';

@Options({
  components: {
    Top_Menu
  },
})

export default class Flower_Info_Upload extends Vue {
  public store = useStore();
  public click_check = false;
  public flower_name_list: string[] = [];
  public selected_Flower: string = "";
  public other_flower_input: boolean = false;
  public percentage_results: boolean = false;
  public flower_results: any | null = null;
  public flower_dis_result: boolean = false;
  public flower_picture: string = "";
  public startX = 0;
  public startY = 0;
  public offsetX = 0;
  public offsetY = 0;

  public img: string = "";
  public img_select: boolean = false;
  public check_img: boolean = false;

  public mounted() {
    console.log(this.store.state.loginId);
    this.judgingWhetherVote();
    this.flowerInfoGet();

    //꽃사진 가져오기
    axios.post('http://localhost:3000/flowerInfoPictureGet', { info_id: this.store.state.flowerInfo.info_id })
      .then(response => {
        this.flower_picture = response.data[0].P_url;
      })
      .catch(error => { console.error('에러:', error); });

    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.addEventListener("mousedown", this.onMouseDown);
    }
  }

  //투표여부 판단
  public judgingWhetherVote() {
    console.log(this.store.state.flowerInfo.info_id)
    axios.post('http://localhost:3000/voteWhether ', { info_id: this.store.state.flowerInfo.info_id, u_id: this.store.state.loginId })
      .then(response => {
        if (response.data[0].count) {
          this.percentage_results = true;
          this.voteResult();
        }
      })
      .catch(error => { console.error('에러:', error); });
  }

  //꽃 판별 유무 확인
  public flowerInfoGet() {
    if (this.store.state.flowerInfo.discrimination === 0) {
      //지금까지 투표된 꽃이름 조회하기
      axios.post('http://localhost:3000/flowerNameSelect', { info_id: this.store.state.flowerInfo.info_id })
        .then(response => {
          for (let i = 0; i < response.data.length; i++) {
            this.flower_name_list[i] = response.data[i].f_name;
          }
        })
        .catch(error => { console.error('에러:', error); });
    }
  }

  // 드롭다운 함수
  public toggleDropdown() {
    this.click_check = !this.click_check;
    const border = document.getElementsByClassName('select-options-box');
    const border_line = border[0] as HTMLElement;

    if (this.click_check) {
      border_line.style.borderBottom = 'none';
    }
    else if (this.selected_Flower) {
      border_line.style.border = '1px solid black';
    }
    else {
      border_line.style.border = '1px dotted black';
    }
  }

  // 꽃 선택시 p태그에 넣고 드롭다운 끄기
  public selectFlower(flower: string) {
    this.selected_Flower = flower;
    const border = document.getElementsByClassName('select-options-box');
    const border_line = border[0] as HTMLElement;

    if (this.selected_Flower) {
      this.other_flower_input = false;
      border_line.style.border = '1px solid black';
    }

    this.click_check = !this.click_check;
  }

  //초기화
  public infoSelectReset() {
    this.selected_Flower = "";
    this.other_flower_input = false;
  }

  //기타 선택시
  public otherFlower() {
    this.selected_Flower = "";
    this.other_flower_input = true;
  }

  //꽃 이름 넣기 및 조회
  public floerNameInput() {
    if (this.selected_Flower) {
      const f_vote = {
        Info_id: this.store.state.flowerInfo.info_id,
        U_id: this.store.state.loginId,
        f_name: this.selected_Flower
      };

      axios.post('http://localhost:3000/flowerNameVote', { f_vote })
        .then(response => {
          this.percentage_results = true;
          this.voteResult();
        })
        .catch(error => { console.error('에러:', error); });

      this.pointEarn();
    } else {
      console.log("이름없음")
    }
  }

  //투표 결과 보기
  public voteResult() {
    axios.post('http://localhost:3000/flowerPercentage', { info_id: this.store.state.flowerInfo.info_id })
      .then(response => {
        this.flower_results = response.data;
      })
      .catch(error => { console.error('에러:', error); });
  }

  //투표결과 수정
  public voteReset() {
    this.percentage_results = false;
    axios.post('http://localhost:3000/voteResultReset', { info_id: this.store.state.flowerInfo.info_id, u_id: this.store.state.loginId })
      .then(response => {
        this.infoSelectReset();
      })
      .catch(error => { console.error('에러:', error); });
  }

  //div 박스에서 이미지업로드
  public imageUpload() {
    const image = this.$refs.file as HTMLInputElement;
    if (image) {
      image.click();
    }
  }

  // 이미지 동적으로 불러오가
  public changeFile(event: any) {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e.target) {
          this.img_select = true;
          this.img = e.target.result as string;
        }
      };
      reader.readAsDataURL(file);
    }
  }


  // 이미지 움직이기
  public beforeDestroy() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.removeEventListener("mousedown", this.onMouseDown);
    }
  }

  public onMouseDown = (e: MouseEvent) => {
    e.preventDefault();

    this.startX = e.clientX;
    this.startY = e.clientY;

    this.offsetX = this.getOffsetLeft() - this.startX;
    this.offsetY = this.getOffsetTop() - this.startY;

    document.addEventListener("mousemove", this.onMouseMove);
    document.addEventListener("mouseup", this.onMouseUp);
  };


  public onMouseMove = (e: MouseEvent) => {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    if (imgObj) {
      imgObj.style.left = `${e.clientX + this.offsetX}px`;
      imgObj.style.top = `${e.clientY + this.offsetY}px`;
    }
  };


  public onMouseUp = () => {
    document.removeEventListener("mousemove", this.onMouseMove);
    document.removeEventListener("mouseup", this.onMouseUp);
  };


  public getOffsetLeft() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    return parseInt(imgObj?.style.left || "0", 10);
  }


  public getOffsetTop() {
    const imgObj = document.getElementById("imgObj") as HTMLImageElement;
    return parseInt(imgObj?.style.top || "0", 10);
  }

  //판별된 꽃사진 올리기
  public flowerPictureUpload() {
    // Canvas 요소 생성
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    // Canvas 크기 설정
    canvas.width = 630; // 캔버스 너비
    canvas.height = 630; // 캔버스 높이

    // 이미지를 Canvas에 그림
    const imgObj = document.getElementById("imgObj") as HTMLImageElement; // 이미지 요소 가져오기

    if (imgObj && ctx) {
      ctx.beginPath();
      ctx.arc(canvas.width / 2, (canvas.height / 2) - 20, (canvas.width / 2) - 50, 0, Math.PI * 2);
      ctx.closePath();
      ctx.clip();
      const imgLeft = this.getOffsetLeft();
      const imgTop = this.getOffsetTop();

      ctx.drawImage(imgObj, imgLeft, imgTop);

      //사진 집어 넣기
      axios.post('http://localhost:3000/flowerPictureUpload', { Info_id: this.store.state.flowerInfo.info_id, id: this.store.state.loginId, url: canvas.toDataURL('image/png') })
        .then(response => {
        })
        .catch(error => { console.error('에러:', error); });
    }
    this.pointEarn();
  }

  //포인트 적립하기
  public pointEarn() {
    axios.post('http://localhost:3000/earnPoints', { point: 5, id: this.store.state.loginId })
      .then(response => {
      })
      .catch(error => { console.error('에러:', error); });
  }
}
