import { createStore } from 'vuex';
import createPersistedState from 'vuex-persistedstate';
import axios from "axios"

export default createStore({
  state: {
    show_links: true,
    loginStatus: false,
    infoId: "",
    flowerInfo: "",
    loginId: "",
  },
  mutations: {
    //페이지 이동시 App.vue요소 없애기
    setShowLinks(state, value) {
      state.show_links = value;
    },

    //로그인 유무
    setLoginStatus(state, value){
      state.loginStatus = value;
    },

    //로그인 아이디
    setLoginId(state, value){
      state.loginId = value;
    },

    //꽃 게시글 저장(info_board)
    setFlowerInfo(state, value) {
      state.flowerInfo = value;
    },

    //게시판 아이디 저장
    setInfoId(state, value) {
      state.infoId = value;
    },

  },
  actions: {

  },
  modules: {},

  plugins: [
    createPersistedState({

    })
  ]
});
