import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
import axios from 'axios';
import { Watch } from 'vue-property-decorator';
import Top_Menu from '../Top_Menu/Top_Menu.vue';

@Options({
  components: {
    Top_Menu
  },
})

export default class Login_Page extends Vue {
  private store = useStore();
  public client_id: string = "809562909145-p1f8o9eh2nlaaiga9svtc2qe275vi19h.apps.googleusercontent.com";
  public redirect_url: string = "https://www.googleapis.com/auth/indexing";
  public member_password: string = "";
  public u_id: string = "";
  public password: string = "";
  public login_warning: boolean = false;
  public password_find_dialog: boolean = false;
  public id_checkbox: boolean = false;

  public mounted() {
  }

  //로그인버튼 클릭후 id, password 조회
  public navigateToLoginPage() {
    const user = {
      U_id: this.u_id,
      U_pass: this.password
    }

    axios.post('http://localhost:3000/login', user)
      .then(response => {
        const dupli = response.data[0]['count(U_id)'];
        //이메일과 비번이 맞을시
        if (dupli === 1) {
          this.store.commit('setLoginStatus', true);
          this.store.commit('setLoginId', this.u_id);
        } else {
          this.login_warning = true;
        }
      })
      .catch(error => {
        console.error('Error:', error);
      });

  }


  //로그인 성공후 스케줄로 이동
  @Watch('store.state.loginStatus')
  public loginSuccess() {
    this.$router.push('/APP');
  }
}


