<template>
    <Top_Menu />
    <Flower_User_Information_Edit_Dialog v-if="use_edit" @editDialog="editDialogClose"/>
    <div class="edit-container">
        <div class="edit-main">
            <div class="edit-insert-main">
                <div class="edit-insert-top">
                    <div class="edit-insert-item">
                        <div class="edit-insert-item-top">
                            <div class="edit-insert-top-left">
                                유저 이름
                            </div>
                            <div class="edit-insert-top-right">
                                &nbsp;
                            </div>
                            <div class="float-clear"></div>
                        </div>
                        <div class="edit-insert-item-bottom">
                            <input class="edit-insert-item-input" type="text" v-model="user_name" required>
                        </div>
                    </div>
                    <div class="edit-insert-item">
                        <div class="edit-insert-item-top">
                            <div class="edit-insert-top-left">
                                메일 주소
                            </div>
                            <div class="edit-insert-top-right">
                                &nbsp;
                            </div>
                            <div class="float-clear"></div>
                        </div>
                        <div class="edit-insert-item-bottom">
                            <input class="edit-insert-item-input" type="text" v-model="user_addres" required>
                        </div>
                    </div>

                    <div class="edit-insert-item">
                        <div class="edit-insert-item-top">
                            <div class="edit-insert-top-left">
                                전화번호
                            </div>
                            <!-- <div class="edit-insert-top-right">
                                숫자 11자로 입력해야 합니다. 
                            </div> -->
                            <div class="float-clear"></div>
                        </div>
                        <div class="edit-insert-item-bottom">
                            <input class="edit-insert-item-input" type="text" v-model="user_phone">
                        </div>
                    </div>

                    <div class="edit-insert-item">
                        <div class="edit-insert-item-top">
                            <div class="edit-insert-top-left">
                                암호 변경
                            </div>
                            <div class="float-clear"></div>
                        </div>
                        <div class="edit-insert-item-bottom">
                            <input class="edit-insert-item-input" type="text" v-model="user_pass">
                        </div>
                    </div>
                    <div>
                        <input class="edit-button" type="button" value="수정 하기" @click="editBtnClick" />
                    </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script src="./Flower_User_Information_Edit.ts"></script>
<style src="./Flower_User_Information_Edit.css"></style>